/* eslint-disable react/no-array-index-key */

import {StaticImage} from 'gatsby-plugin-image';
import React, {useState} from 'react';
import {useInView} from 'react-intersection-observer';
import {usePageVisibility} from 'react-page-visibility';
import Ticker from 'react-ticker';

import useWindowWidth from '../../hooks/useWindowWidth';
import twConfig from '../../utils/twConfig';
import Button from '../common/Button';
import {RoundArrow} from '../common/icons';

const mdScreen = parseInt(twConfig.theme.screens.md, 10);

const creators = [
  {
    name: 'Surreal Entertainment',
    value: '2,367,400',
    link: '/',
    image: <StaticImage
      src="../../images/surreal-entertainment.jpg"
      quality={90}
      className="h-full"
      alt="Surreal Entertainment"
      placeholder="blurred"
    />,
  },
  {
    name: 'Eddie Van Der Meer',
    value: '2,360,117',
    link: '/',
    image: <StaticImage
      src="../../images/eddie-van-der-meer.jpg"
      quality={90}
      className="h-full"
      alt="Eddie Van Der Meer"
      placeholder="blurred"
    />,
  },
  {
    name: 'Fainted',
    value: '1,602,320',
    link: '/',
    image: <StaticImage
      src="../../images/fainted.jpg"
      quality={90}
      className="h-full"
      alt="Fainted"
      placeholder="blurred"
    />,
  },
  {
    name: 'Without Music',
    value: '3,136,701',
    link: '/',
    image: <StaticImage
      src="../../images/without-music.jpg"
      quality={90}
      className="h-full"
      alt="Without Music"
      placeholder="blurred"
    />,
  },
  {
    name: 'Scrubby',
    value: '1,907,002',
    link: '/',
    image: <StaticImage
      src="../../images/scrubby.jpg"
      quality={90}
      className="h-full"
      alt="Scrubby"
      placeholder="blurred"
    />,
  },
];

const creatorsMobile1 = creators.slice(0, Math.floor(creators.length / 2));
const creatorsMobile2 = creators.slice(Math.floor(creators.length / 2));

const Card = ({
  name, value, link, image,
}) => (
  <a
    href={link}
    className="block relative w-96 md:w-72 lg:w-80 h-150 md:h-110 lg:h-120 mr-12 overflow-hidden rounded-3xl border border-n-400 border-opacity-50 z-0"
  >
    <div className="absolute h-full">
      {image}
    </div>
    <div className="relative h-full flex flex-col justify-end px-12 py-7 text-2xl bg-noise animate-noise">
      <p className="text-n-200 uppercase pb-3">{name}</p>
      <p className="text-primary font-mono">{value}</p>
    </div>
  </a>
);

const OurCreators = () => {
  const isTabVisible = usePageVisibility();
  const [animationRef, animationInView] = useInView();

  const windowWidth = useWindowWidth();
  const isMdScreen = windowWidth >= mdScreen;

  const [firstTickerHovered, setFirstTickerHovered] = useState(false);
  const [secondTickerHovered, setSecondTickerHovered] = useState(false);

  return (
    <section className="relative bg-n-500">
      <StaticImage
        src="../../images/our-creators-background.jpg"
        quality={90}
        alt="What We Offer"
        objectPosition="right center"
        className="absolute inset-x-0 bottom-0"
        style={{position: 'absolute'}}
      />
      <div className="relative pt-72 pb-80 bg-noise animate-noise">
        <div className="container grid grid-cols-8 md:gap-x-20 mb-44">
          <div className="col-span-8 md:col-span-1 lg:col-span-1 mb-16">
            <StaticImage
              src="../../images/gem-icon.png"
              alt="Our Creators"
              className="w-16"
            />
          </div>
          <div className="col-span-8 md:col-span-6 lg:col-span-4 2xl:col-span-3">
            <h2 className="text-6xl text-n-200 uppercase mb-10 lg:mb-14">Our Creators</h2>
            <p className="text-n-400 font-mono text-justify mb-14 lg:mb-0">
              Were invitation-only platform that works with some of the biggest and most recognizable names on YouTube.
            </p>
          </div>
          <div className="md:col-start-2 lg:col-start-6 col-span-8 md:col-span-4 lg:col-span-3 flex lg:justify-end items-end">
            <Button icon={<RoundArrow className="transform -rotate-90" />}>Request an Invite</Button>
          </div>
        </div>
        <div ref={animationRef}>
          {isTabVisible && (
            <>
              <div
                className="mb-20"
                onMouseOver={() => setFirstTickerHovered(true)}
                onMouseLeave={() => setFirstTickerHovered(false)}
                onTouchStart={() => setFirstTickerHovered(true)}
                onTouchEnd={() => setFirstTickerHovered(false)}
                onFocus={() => setFirstTickerHovered(true)}
                onBlur={() => setFirstTickerHovered(false)}
              >
                <Ticker
                  move={animationInView}
                  speed={firstTickerHovered ? 2 : 5}
                >
                  {({index}) => {
                    const creatorsToUse = isMdScreen ? creators : creatorsMobile1;
                    const creator = creatorsToUse[index % creatorsToUse.length];
                    return <Card {...creator} />;
                  }}
                </Ticker>
              </div>
              {!isMdScreen && (
                <div
                  onMouseOver={() => setSecondTickerHovered(true)}
                  onMouseLeave={() => setSecondTickerHovered(false)}
                  onTouchStart={() => setSecondTickerHovered(true)}
                  onTouchEnd={() => setSecondTickerHovered(false)}
                  onFocus={() => setSecondTickerHovered(true)}
                  onBlur={() => setSecondTickerHovered(false)}
                >
                  <Ticker
                    move={animationInView}
                    direction="toRight"
                    offset={1000}
                    speed={secondTickerHovered ? 2 : 5}
                  >
                    {({index}) => {
                      const creator = creatorsMobile2[index % creatorsMobile2.length];
                      return <Card {...creator} />;
                    }}
                  </Ticker>
                </div>
              )}
            </>
          )}
        </div>
      </div>
    </section>
  );
};

export default OurCreators;
