const Question = ({
  title, children, opened, setOpened,
}) => (
  <div className={`py-12 px-5 md:px-10 border-t ${opened ? 'border-n-500 border-opacity-70' : 'border-n-400'}`}>
    <button
      type="button"
      className="w-full flex"
      onClick={() => setOpened()}
    >
      <h3 className="text-left text-2xl uppercase md:pr-40 flex-1">{title}</h3>
      <div className="hidden md:flex w-4 h-4 mt-2 items-center justify-center">
        <StaticImage
          src="../../images/minus.png"
          alt="Close"
          className="w-3"
          style={{display: !opened ? 'none' : 'block'}}
        />
        <StaticImage
          src="../../images/plus.png"
          alt="Open"
          className="w-4"
          style={{display: opened ? 'none' : 'block'}}
        />
      </div>
    </button>
    <div className={`overflow-hidden grid grid-cols-1 lg:grid-cols-2 gap-10 lg:gap-24 pr-10 font-mono text-justify transition-all ${!opened ? 'h-0 opacity-0' : 'pt-14 opacity-100'}`}>
      {children}
    </div>
  </div>
);

export default () => {
  const [showAll, setShowAll] = useState(false);
  const [openedQuestion, setOpenedQuestion] = useState(null);

  return (
    <section className="bg-n-200 pt-48 pb-36 relative bg-noise animate-noise">
      <div className="relative container grid grid-cols-8">
        <StaticImage
          src="../../images/faq.jpg"
          alt="FAQ"
          className="hidden lg:block absolute top-110 2xl:top-80 -left-20 xl:-left-14 w-80"
          style={{position: 'absolute'}}
          quality={90}
          placeholder="blurred"
          layout="fullWidth"
        />
        <div className="col-span-8 lg:col-span-6 lg:col-start-3">
          <h2 className="text-8xl uppercase mb-20">
            <span className="hidden md:inline leading-tight">Frequently asked questions</span>
            <span className="inline md:hidden">FAQ</span>
          </h2>
          <div className="border-b border-n-400 lg:-ml-4">
            {(showAll ? questions : questions.slice(0, 5)).map((item, i) => (
              <Question
                title={item.title}
                key={item.title}
                opened={i === openedQuestion}
                setOpened={() => (i !== openedQuestion ? setOpenedQuestion(i) : setOpenedQuestion(null))}
              >{item.text}
              </Question>
            ))}
          </div>
          <div className="pt-14">
            {!showAll && (
            <Button
              icon={<RoundArrow className="transform" />}
              onClick={() => setShowAll(true)}
            >Load more
            </Button>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};
