import {StaticImage} from 'gatsby-plugin-image';
import React from 'react';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import {useInView} from 'react-intersection-observer';
import {Carousel} from 'react-responsive-carousel';

const services = [
  {
    name: 'Music catalogs',
    textTitle: 'Music Catalogs',
    text: 'We pride ourselves in helping Creators navigate the technical side of YouTube, and keeping their channels safe.',
  },
  {
    name: 'Management',
    textTitle: 'Sponsorship Management',
    text: 'We pride ourselves in helping Creators navigate the technical side of YouTube, and keeping their channels safe.',
  },
  {
    name: 'Brand integrations',
    textTitle: 'Brand Integrations',
    text: 'We pride ourselves in helping Creators navigate the technical side of YouTube, and keeping their channels safe.',
  },
  {
    name: 'Paid Media',
    textTitle: 'Paid Media',
    text: 'We pride ourselves in helping Creators navigate the technical side of YouTube, and keeping their channels safe.',
  },
  {
    name: 'Copyright Claims',
    textTitle: 'Copyright Claims',
    text: 'We pride ourselves in helping Creators navigate the technical side of YouTube, and keeping their channels safe.',
  },
  {
    name: 'Premium support',
    textTitle: 'Premium Support',
    text: 'We pride ourselves in helping Creators navigate the technical side of YouTube, and keeping their channels safe.',
  },
];

const Service = ({children, active, setActive}) => {
  const activeClass = active ? 'text-primary lg:text-primary' : 'text-n-600 lg:text-n-400';
  return (
    <button
      type="button"
      className={`block mt-4 lg:mt-10 text-4xl sm:text-6xl uppercase text-left hover:text-primary ${activeClass}`}
      onClick={setActive}
    >
      {children}
    </button>
  );
};

const ServicesList = ({items, activeService, setActiveService}) => {
  const [sliderRef, sliderInView] = useInView();

  return (
    <div ref={sliderRef}>
      <div className="hidden lg:block">
        {items.map((service, i) => (
          <Service
            active={activeService === i}
            setActive={() => setActiveService(i)}
            key={service.name}
          >
            {service.name}
          </Service>
        ))}
      </div>
      <Carousel
        autoPlay={sliderInView}
        infiniteLoop
        dynamicHeight
        className="lg:hidden carousel-overflow-visible"
        width="85%"
        onChange={i => setActiveService(i)}
        selectedItem={activeService}
        showArrows={false}
        showIndicators={false}
        showStatus={false}
        showThumbs={false}
      >
        {items.map((service, i) => (
          <div
            key={service.name}
            className="pr-16"
          >
            <Service
              active={activeService === i}
              setActive={() => setActiveService(i)}
            >
              {service.name}
            </Service>
          </div>
        ))}
      </Carousel>
    </div>
  );
};

const ServiceText = ({className, service}) => (
  <p className={`font-mono text-justify text-sm leading-7 ${className || ''}`}>
    <span className="font-bold">{service.textTitle}: </span>
    {service.text}
  </p>
);

export default () => {
  const [activeService, setActiveService] = React.useState(0);

  return (
    <section className="relative bg-n-500">
      <StaticImage
        src="../../images/what-we-offer-background.jpg"
        quality={90}
        alt="What We Offer"
        objectPosition="right center"
        className="absolute top-0 right-0 max-w-1/2 max-h-full"
        style={{position: 'absolute'}}
      />
      <div className="relative pt-32 lg:pb-40 bg-noise animate-noise">
        <div className="container grid grid-cols-8 mb-96 gap-y-44 lg:gap-0">
          <div className="lg:pr-12 row-start-2 lg:row-start-1 col-span-8 md:col-span-6 lg:col-span-3 relative md:left-14">
            <StaticImage
              src="../../images/mail-icon.png"
              alt="What We Offer"
              className="w-16"
              quality={80}
              placeholder="blurred"
            />
            <p className="font-mono text-n-300 text-justify mt-14">
              Because of the resources we dedicate to each Creator, we are invitation-only.
              However, if you feel that your channel is a good fit for the network, you can
              send us a request, and one of our representatives will reach out to you.
            </p>
          </div>
          <div className="col-span-8 lg:col-span-5 -mt-44 md:-mt-56 relative md:left-14 lg:left-24">
            <StaticImage
              src="../../images/what-we-offer-1.jpg"
              alt="What We Offer"
              className="h-128 xl:h-150 w-screen md:w-220 lg:w-170 xl:w-225"
              quality={90}
              placeholder="blurred"
            />
          </div>
        </div>
        <div className="container grid grid-cols-8 gap-y-56 text-n-400">
          <div className="relative col-span-8 lg:col-span-3 -mt-40">
            <StaticImage
              src="../../images/what-we-offer-3.jpg"
              alt="What We Offer"
              className="lg:hidden absolute inset-0 transform rotate-6 opacity-60"
              style={{position: 'absolute'}}
              layout="fullWidth"
              quality={90}
              placeholder="blurred"
              objectPosition="left center"
            />
            <StaticImage
              src="../../images/what-we-offer-2.jpg"
              alt="What We Offer"
              quality={90}
              placeholder="blurred"
            />
            <ServiceText
              className="hidden lg:block mt-10 w-4/5 mx-auto"
              service={services[activeService]}
            />
          </div>
          <div className="min-h-260 col-span-8 lg:col-span-4 lg:col-start-5">
            <h2 className="font-mono text-lg uppercase">What we offer</h2>
            <ServicesList
              items={services}
              activeService={activeService}
              setActiveService={setActiveService}
            />
            <ServiceText
              className="mt-14 lg:hidden max-w-md"
              service={services[activeService]}
            />
          </div>
        </div>
        <div className="lg:hidden relative h-128 mt-96 md:mt-64">
          <div className="absolute left-1/2 transform -translate-x-1/2 flex space-x-16">
            <StaticImage
              src="../../images/what-we-offer-3.jpg"
              alt="What We Offer"
              className="h-128 w-screen-1/2 rounded-3xl z-0"
              quality={90}
              placeholder="blurred"
              objectPosition="right center"
            />
            <StaticImage
              src="../../images/what-we-offer-4.jpg"
              alt="What We Offer"
              className="h-128 w-screen-1/2 rounded-3xl z-0 -mt-56 md:-mt-24"
              quality={90}
              placeholder="blurred"
              objectPosition="left center"
            />
          </div>
        </div>
      </div>
    </section>
  );
};
